.services .banner {
    margin-bottom: 40px;
    background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.8)), url("../../imgs/quantum-ex-capital-solutions/2.jpg");

}


.page .container-fluid {
    margin-top: 80px;
    margin-bottom: 250px;
    display: flex;
    flex-direction: column;
}


.page .filter-items {
    display: flex;
    flex-direction: column;
    gap: 20px;
    /* margin-top: ; */
}

.page .title, .page .filter h3 {
    min-height: 40px;
    margin-bottom: 15px;
}

.page .filter-items .filter-item {
    display: flex;
    align-items: center;
    gap: 10px;
    color: var(--cl--9--);
    font-weight: 600;
}



.page .filter-items .filter-item.active {
    color: var(--cl--10--);
}

.page .filter-items .filter-item.active .icon{
    color: var(--cl--2--);
}

.page .filter-items .filter-item:hover {
    color: var(--cl--10--);
}




.page .images {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: start; */
}

/* .page .images::after {
    content: "";
    flex-basis: 350px;
} */

.page .images .image-container {
    flex-basis: 100%;
    
}

.page .images .image-card {
    height: 190px;    
    position: relative;
    cursor: pointer;
}

.page .images .image-card .overlay{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(57, 57, 57, 0.7);
    top: 0;
    left: 0;
    transform: scale(0);
    transform: all 0.2s 0.1s ease-in-out;
    color: var(--cl--1--);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.page .images .image-card .overlay a{
    color: var(--cl--6--);
}

.page .images .image-card .overlay a:hover{
    color: var(--cl--1--);
}



.page .images .image-card:hover .overlay{
    transform: scale(1);
}

.page .images .image-container .image-card img{
    object-fit: cover;
    max-width: 100%;
    height: 100%;
    vertical-align: middle;
    border-radius: 5px;
}




.page .article .title {
    font-weight: 600;
    text-transform: uppercase;
}


.page .subservice {
    margin-bottom: 40px;
}

.page .article img {
    margin-bottom: 30px;
}

.page .article .heading{
    margin-bottom: 10px;
    font-weight: 500;
}

.page .article .subheading{
    display: block;
    margin-bottom: 3px;
    font-style:italic;
    font-weight: 500;
}

.page .article .body-text{
    margin-bottom: 25px;
}


.page .article .contact .contact-us-link {
    font-weight: 500;
}